import { Component, OnInit } from '@angular/core';
import { FormControl , FormGroup, Validators } from  '@angular/forms';
import { ToasterConfig } from 'angular2-toaster';
import { ProductService } from '../../services/product/product.service';

import 'style-loader!angular2-toaster/toaster.css';
import {
  NbComponentStatus,
  NbGlobalLogicalPosition,
  NbGlobalPhysicalPosition,
  NbGlobalPosition,
  NbToastrService,
} from '@nebular/theme';

@Component({
  selector: 'ngx-addproduct',
  templateUrl: './addproduct.component.html',
  styleUrls: ['./addproduct.component.scss']
})
export class AddproductComponent implements OnInit {
  imageUrls:any = [];
  formGroup:FormGroup;
  submited:boolean = false;
  config: ToasterConfig;
  uploading:boolean = false;

  constructor(private toastrService: NbToastrService,private productService:ProductService) {}

  ngOnInit() {
    this.formGroup = new FormGroup({
      name:new FormControl('',[Validators.required]),
      description:new FormControl('',[Validators.required]),
      price:new FormControl('',[Validators.required]),
      incrementedBy:new FormControl('',[Validators.required,]),
      discount:new FormControl('',[Validators.required]),
      category:new FormControl('',[Validators.required]),
      subCategory:new FormControl('',[Validators.required]),
      subCategoryName:new FormControl(''),
      units:new FormControl(''),
      quantity_remaining:new FormControl('100.0')
    });
    this.getCategories();
  }

  addImage($event){

    if($event.target.files && $event.target.files[0]){

      let render = new FileReader();

      render.onload = (et)=>{

        let extension = $event.target.files[0].name.split('.')[$event.target.files[0].name.split('.').length -1];
        console.log(extension);
        if(['jpg','png','jpeg'].indexOf(extension) > -1){
          this.imageUrls.push(
            {
              file:$event.target.files[0],
              imageUrl:render.result
            }

          );
        }
        else{
          this.showToaster("OOPS !","Please Upload jpg , png , jpeg format .",'primary');
        }
        console.log(this.imageUrls);
      };
      render.readAsDataURL($event.target.files[0]);

    }

  }
  deleteImage(index){
    console.log("Data deleted");
    // this.imageUrls = [];
    this.imageUrls.splice(index,1);
  }


  onSubmit(){
    // this.formGroup.clearValidators;
    console.log(this.formGroup.value);

    if(this.imageUrls.length == 0){
      this.showToaster("OOPS !","Please Upload one image",'danger');
      return null;
    }

    this.submited = true;
    if(this.formGroup.valid){
      if(this.formGroup.get('subCategory').value > 0 ){
        const formData = new FormData();
      formData.append('product_name',this.formGroup.get('name').value);
      formData.append('product_description',this.formGroup.get('description').value);
      formData.append('product_price',this.formGroup.get('price').value);
      formData.append('incremented_by',this.formGroup.get('incrementedBy').value);
      formData.append('category_id',this.formGroup.get('category').value);
      formData.append('sub_category_id',this.formGroup.get('subCategory').value);
      let requestData = {
        "product_name":this.formGroup.get('name').value,
        "product_description":this.formGroup.get('description').value,
        "product_price":this.formGroup.get('price').value,
        "incremented_by":this.formGroup.get('incrementedBy').value,
        "category_id":this.formGroup.get('category').value,
        "sub_category_id":this.formGroup.get('subCategory').value,
        "units":this.formGroup.get('units').value,
        "quantity_remaining":this.formGroup.get('quantity_remaining').value
      };
      console.log(requestData);
        this.uploadProduct(requestData);

      }
      else{
        if(this.formGroup.get('subCategory').value < 1 && this.formGroup.get('subCategoryName').value.length != 0){
          const formData = new FormData();
      formData.append('product_name',this.formGroup.get('name').value);
      formData.append('product_description',this.formGroup.get('description').value);
      formData.append('product_price',this.formGroup.get('price').value);
      formData.append('incremented_by',this.formGroup.get('name').value);
      formData.append('category_id',this.formGroup.get('name').value);
      formData.append('sub_category_name',this.formGroup.get('subCategoryName').value);
      let requestData = {
        "product_name":this.formGroup.get('name').value,
        "product_description":this.formGroup.get('description').value,
        "product_price":this.formGroup.get('price').value,
        "incremented_by":this.formGroup.get('incrementedBy').value,
        "category_id":this.formGroup.get('category').value,
        "sub_category_name":this.formGroup.get('subCategoryName').value,
        "units":this.formGroup.get('units').value,
        "quantity_remaining":this.formGroup.get('quantity_remaining').value
      };
      console.log(requestData);
      this.uploadProduct(requestData);
        }
        else{
          this.showToaster("OOPS !","Please Provide Sub Category Name",'danger');
        }
      }




    }


  }


  uploadProduct(data:any){
    this.uploading = true;
    this.count = 0;
    this.productService.postMethod((response)=>{
      console.log(response);

      this.uploadImage(response.id);
      this.uploadDiscount(response.id);
    },(error)=>{console.log(error);this.uploading = false;},data,'product/0/');


  }
  count:number = 0;

  uploadImage(id){
    let files = [];
    const formData = new FormData();

    this.imageUrls.forEach((val)=>{
      // files.push(val.file);
      formData.append("images",val.file,val.file.name);
    });

    this.productService.postMethod((response)=>{
      console.log(response);
      this.count = this.count + 1;
      if(this.count == 2){
        alert("Product Uploaded");
        this.uploading = false;
      }
    },(error)=>{if(this.count == 2){
      this.uploading = false;
    }console.log(error);},formData,'product/image/' + id + '');

  }

  uploadDiscount(id){
    this.productService.postMethod((response)=>{
      console.log(response);
      this.count = this.count + 1;
      if(this.count == 2){
        alert("Product Uploaded");
        this.uploading = false;
      }
    },(error)=>{if(this.count == 2){
      this.uploading = false;
    }console.log(error);},{	"product_discount":this.formGroup.get('discount').value},'product/discount/' + id + '');
  }



  showToaster(title,body,status){
    const config = {
      status:status ,
      destroyByClick:false,
      duration: 2000,
      hasIcon: true,
      position:NbGlobalLogicalPosition.BOTTOM_END,
      preventDuplicates: false,
    };



    this.toastrService.show(
      body,
      title,
      config);
  }

  allCategory:any = [];
  subCategory:any = [];
  getCategories(){

    this.productService.getMethod(
      (response)=>{

        this.allCategory = Object.keys(response).map((key)=>{
          return {
            categoryId:response[key].category_id,
            categoryname:response[key].category_name,
            subCategory:response[key].sub_categories
          }
        });

        console.log(response);
      },
      (error)=>{

        console.log(error);
      },
      {},
      "product/all/categories"
    );

  }
  onCategoryChange(subCategoryData:any){
    this.subCategory = subCategoryData;
    console.log(this.subCategory);
  }


}
