import { Component, OnInit } from '@angular/core';
import { FormControl,FormGroup , Validators } from '@angular/forms';
import { ProductService } from '../../services/product/product.service';
import { ToasterConfig } from 'angular2-toaster';
import {
  NbComponentStatus,
  NbGlobalLogicalPosition,
  NbGlobalPhysicalPosition,
  NbGlobalPosition,
  NbToastrService,
} from '@nebular/theme';

@Component({
  selector: 'ngx-addcategories',
  templateUrl: './addcategories.component.html',
  styleUrls: ['./addcategories.component.scss']
})
export class AddcategoriesComponent implements OnInit {
  formGroup:FormGroup;
  uploading:boolean = false;
  submited:boolean = false;
  config: ToasterConfig;

  constructor(private productService:ProductService,private toastrService: NbToastrService) { }

  ngOnInit() {
    this.formGroup = new FormGroup({
      name:new FormControl('',[Validators.required]),
      file:new FormControl('',[Validators.required])
    });
  }

  uploadImage($event){

    if($event.target.files.length > 0)
    {

      this.formGroup.patchValue({
        file:$event.target.files[0]
      });

    }
  }
  onSubmit(){
    if(this.formGroup.valid){
      const formData = new FormData();
      formData.append('categories_image',this.formGroup.get('file').value);
      formData.append('category_name',this.formGroup.get('name').value);
      this.uploading = true;
      this.productService.postMethod(
        (response)=>{
          this.uploading = false;
          this.showToaster("Success !!","The category was successfully added.","primary");
          console.log(response);
        },
        (error)=>{
          this.uploading =  false;
          this.showToaster("OOPS !!","Please try again with other data.","danger");
          console.log(error);
        },
        formData,
        "product/category/upload/0"
      );
    }
    else{
      this.showToaster("OOPS !!","Please give every detail of category","danger");
    }
  }

  showToaster(title,body,status){
    const config = {
      status:status ,
      destroyByClick:false,
      duration: 2000,
      hasIcon: true,
      position:NbGlobalLogicalPosition.BOTTOM_END,
      preventDuplicates: false,
    };



    this.toastrService.show(
      body,
      title,
      config);
  }

}
