import { Component, OnInit } from '@angular/core';
import { Location, } from '@angular/common';
import { Router } from '@angular/router';
import { ProductService } from '../../services/product/product.service';
import { imageBaseUrl } from '../../Models/common';
@Component({
  selector: 'ngx-productdetails',
  templateUrl: './productdetails.component.html',
  styleUrls: ['./productdetails.component.scss']
})
export class ProductdetailsComponent implements OnInit {

  imageUrls:any = [];
  productDetails:any;
  loading:boolean = true;
  baseUrl :any = imageBaseUrl;
  constructor(private router:Router,private productService:ProductService) {

  }

  ngOnInit() {

this.getProductDetails();

  }

  getProductDetails(){
    this.loading = true;
    let splited = this.router.url.split('/');
    let id = splited[splited.length - 1];
    this.productService.getMethod((response)=>{
      console.log(response);
      this.loading = false;
      this.productDetails = response;
    },(error)=>{console.log(error);},{},`product/${id}/`);
  }

  addImage($event){

    if($event.target.files && $event.target.files[0]){

      let render = new FileReader();

      render.onload = (et)=>{
        this.imageUrls.push(
          {
            file:$event.target.files[0],
            imageUrl:render.result
          }

        );
        console.log(this.imageUrls);
      };
      render.readAsDataURL($event.target.files[0]);

    }

  }

  checkForNewImage(){
    if(this.imageUrls.length > 0){
      this.loading = true;
    let splited = this.router.url.split('/');
    let id = splited[splited.length - 1];
      this.uploadImage(id);
    }
    else{
      alert("Please upload image to send.")
    }
  }

  uploadImage(id){
    let files = [];
    const formData = new FormData();

    this.imageUrls.forEach((val)=>{
      // files.push(val.file);
      formData.append("images",val.file,val.file.name);
    });

    this.productService.postMethod((response)=>{
      this.getProductDetails();
      alert("Image added successfully");
      this.imageUrls = [];
    },(error)=>{console.log(error);},formData,'product/image/' + id + '');

  }

}
