import { Component, OnInit } from '@angular/core';
import { ProductDetailsForTile, ProductDetails, AllProductResponse, Paginator } from '../../Models/product-models';
import { ProductService } from '../../services/product/product.service';

@Component({
  selector: 'ngx-allproducts',
  templateUrl: './allproducts.component.html',
  styleUrls: ['./allproducts.component.scss']
})
export class AllproductsComponent implements OnInit {

  allProductDetails:any= [];
  product:any;
  paginator:any;
  p:number = 1;
  categoryId = 0;
  subCategoryId = 0;
  loading:boolean = true;
  allCategory:any = [];
  subCategory:any = [];

  constructor(private productService:ProductService) {

   }

  ngOnInit() {

    this.getProductData(1);
    this.getCategories();




  }

  onCategoryChange(subCategoryData:any){
    this.subCategory = subCategoryData;
    console.log(this.subCategory);
  }

  getCategories(){

    this.productService.getMethod(
      (response)=>{

        this.allCategory = Object.keys(response).map((key)=>{
          return {
            categoryId:response[key].category_id,
            categoryname:response[key].category_name,
            subCategory:response[key].sub_categories
          }
        });

        console.log(response);
      },
      (error)=>{
        console.log(error);
      },
      {},
      "product/all/categories"
    );

  }


  getProductData(pageNos):void{

    let x = {};
    if(this.categoryId !=0){
      x["category_id"] = this.categoryId;

    }
    if(this.subCategoryId !=0){
      x["sub_category_id"] = this.subCategoryId;
    }
    console.log(x);
    this.productService.getMethod((response)=>{
      this.loading = false;
      console.log(response);
        this.allProductDetails = response.data;
        this.paginator = response.pagnigation;
        console.log(this.paginator);
    },(error)=>{

    },x,"product/all/"+pageNos);
  }



  changePage($event){

    this.getProductData($event);

  }


  categoryChange($event){
    // console.log($event);
    this.categoryId = $event;
  }

  subCategoryChange($event){
    this.subCategoryId = $event;
  }

}
