import { Component, OnInit } from '@angular/core';
import { ProductService } from '.././../services/product/product.service';

@Component({
  selector: 'ngx-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.scss']
})
export class CategoriesComponent implements OnInit {
  category:any = [];
  constructor(private productService:ProductService) { }

  ngOnInit() {
    this.productService.getMethod(
      (response)=>{
        this.category = Object.keys(response).map((key)=>{
          return {
            category_id:response[key].category_id,
            category_name:response[key].category_name,
            sub_categories:response[key].sub_categories
          }
        });
        console.log(this.category.length);
      },
      (error)=>{},
      {},
      "product/all/categories"
    );
  }

}
