import { Component, OnInit,Input } from '@angular/core';
import { ProductDetailsForTile, ProductDetails } from '../../../Models/product-models';
import {baseUrl,imageBaseUrl} from '../../../Models/common';

@Component({
  selector: 'ngx-productcard',
  templateUrl: './productcard.component.html',
  styleUrls: ['./productcard.component.scss']
})
export class ProductcardComponent implements OnInit {
  @Input() productDetails:any;
  discount:string = '0.0';
  image:string = 'assets/images/kitten-default.png';

  constructor() { }

  ngOnInit() {



    if(this.productDetails.discount.length == 1){
      this.discount = this.productDetails.discount[0].product_discount;
    }

    if(this.productDetails.images.length > 0){
      // console.log(this.productDetails.images);
      this.image = imageBaseUrl + this.productDetails.images[0].product_image;
    }


  }

}
